@charset "UTF-8";
.images-block {
  width: 50%;
  padding-right: 150px;
  position: relative;
  padding: 0 150px 0 0; }
  @media screen and (max-width: 1199px) {
    .images-block {
      padding-right: 70px;
      flex-shrink: 0; } }
  @media screen and (max-width: 768px) {
    .images-block {
      width: calc(100% - 30px);
      margin: 0 auto; } }
  .images-block__wrap {
    position: relative; }
  .images-block__bg {
    width: 100%; }
  .images-block__img {
    width: 57.2%;
    position: absolute;
    right: 0;
    top: 22.9%;
    z-index: 100; }
    .images-block__img img {
      width: 100%; }
    @media screen and (max-width: 1199px) {
      .images-block__img {
        top: 55px; } }
